img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
}

.page {
  width: 100%;
}

.page__header {
  height: 100px;
  width: 100%;
}

.page__header__container {
  height: 100%;
  display: flex;
  margin-left: 3vw;
  margin-right: 8.5vw;
  justify-content: space-between;
}

.title__calendar {
  font-size: 32px;
  margin: auto 0px;
}

.slider {
  margin: auto 0px auto;
  width: 272px;
  height: 35px;
}

.slider__form {
  margin: auto 0px;
  display: flex;
}

.slider__form__button {
  display: flex;
}

.slider__month {
  vertical-align: middle;
  font-size: 22px;
  margin: auto;
}

.slider__but {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  margin: auto 0;
  background-color: #FFFFFF;
  vertical-align: middle;
  transition: all 0.15s ease 0s;
}

.slider__but:hover {
  background-color: #FDF5ED;
  transition: all 0.15s ease 0s;
}

.slider__but__image {
  vertical-align: middle;
  margin: auto;
}

.search {
  margin: auto 0px;
  display: flex;
  width: 30vw;
  height: 50px;
  background: #ffffff;
  box-shadow: 0px 8px 24px 5px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  justify-content: space-between;
  cursor: pointer;
}

.search__lupa {
  margin: auto 1vw;
  vertical-align: middle;
  cursor: pointer;
}

.search__input {
  width: 80%;
  height: 100%;
  cursor: pointer;
  color: #CEA7A1;
}

#preloader_malc {
  position: fixed;
  background: rgba(0, 0, 0, 0.6);
  z-index: 99;
}

#preloader_div {
  color: #111;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  position: fixed;
  z-index: 999;
  margin: auto;
}

.accordion {
  margin: auto 0px;
  width: 30vw;
  height: 50px;
}

.accordion .search {
  cursor: pointer;
}

.accordion .search .search__slide {
  width: 28px;
  height: 28px;
  margin: auto 1vw;
  background-color: #ffffff;
  vertical-align: middle;
  border-radius: 50%;
  transition: all 0.15s ease 0s;
  cursor: pointer;
}

.accordion .search .search__slide:hover {
  background-color: #F8F2F2;
  transition: all 0.15s ease 0s;
}

.accordion .search .search__slide__image {
  vertical-align: middle;
  margin: auto;
}

.accordion.active .search {
  border-radius: 15px 15px 0px 0px;
}

.accordion.active .search .search__slide {
  transform: rotate(90deg);
}

.accordion.active .content {
  max-height: 550px;
  transition: 0.2s;
  background-color: #ffffff;
  border-radius: 0px 0px 15px 15px;
  box-shadow: 0px 24px 24px 5px rgba(0, 0, 0, 0.15);
}

.accordion .content {
  position: relative;
  max-height: 0;
  overflow: hidden;
  transition: 0.2s;
}

.filter__chapter {
  display: flex;
  justify-content: space-between;
  margin: 10px 1vw;
  height: 35px;
}

.filter__title {
  min-width: 145px;
  margin: auto;
}

.filter__accordion {
  display: flex;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.filter__accordion__container {
  width: 100%;
  background-color: #F8F2F2;
  border-radius: 5px;
}

.filter__accordion__container .label {
  line-height: 35px;
  color: #CEA7A1;
  width: 100%;
  display: flex;
  margin: auto 10px;
  justify-content: space-between;
}

.filter__accordion__container .filtr_content {
  position: relative;
  max-width: 0;
  max-height: 0;
  overflow: hidden;
  transition: 0.2s;
  z-index: 10000000;
}

.filter__accordion__container .filtr_content_part {
  line-height: 20px;
  margin: auto 10px;
  border-radius: 5px;
}

.filter__accordion__container .filtr_content_part:hover {
  background-color: #f4e0e0;
}

.filter__accordion__container.active {
